import { combineReducers } from 'redux';

import { ADD_USER, DELETE_USER } from './actions';

const users = (state = [], action) => {
  switch (action.type) {
    case ADD_USER:
      return [
        ...state,
        action.user
      ];
    case DELETE_USER:
      return state.filter((user, idx) => {
        return idx !== action.idx;
      });
    default:
      return state;
  }
};

const app = combineReducers({ users });

export default app;



